<template>
  <div class="tang_detail">
	  	<div class="more" @click="goHome">查看更多</div>
		<div class="name">{{query.name}}</div>
		<div class="author"><span>--- {{query.author}}</span></div>
		<div class="content">
			<div v-html="detail.content"></div>
		</div>
		<div class="explanation">
			<div class="title">名词解析：</div>
			<div v-html="detail.explanation"></div>
		</div>
		<div class="explanation">
			<div class="title">诗词赏析：</div>
			<div v-html="detail.appreciation"></div>
		</div>
		
    
  </div>
</template>

<script>
import wxshare from '../utils/wxshare'
export default {
  name: 'tang_detail',
  data(){
		return{
			query:'',
			detail:'',
		}
	},
	methods:{
		getData(){
			let self = this;
			let query = {detailid:this.query.detailid};
			this.$store.dispatch('getData/getTangshiDetail', query).then((res) => {
				console.log(res)
				this.detail = res.data
				wxshare({
					url:self.$route.fullPath,
					title:self.detail.title,
					desc:self.detail.appreciation.match(/[\u4e00-\u9fa5]/g).join(""),
					imgUrl:'https://img.it610.com/image/info10/cea8417a946c47ccabbbecad8ec442cd.jpg',
				})
			})
		},
		goHome(){
			this.$router.push({
					path: "/home",
					query: '',
				});
		}
	},
	created() {
		
	},
	activated(){
		this.query = this.$route.query
		this.getData()
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.tang_detail{
	text-align: center;
	font-size: .32rem;
	min-height: 13.34rem;
	background: url(http://img.asj.com.cn/d/file/sucai/beijing/h5/525vqqmz1sfbde.jpg-sc.nydt);
	background-size: 100% 100%;
	background-attachment: fixed;
	.more{
		position: absolute; padding: .4rem;
		right: .4rem;
		color: blueviolet;
	}
	.name{
		font-size: .4rem;
		padding: .2rem;
	}
	.author{
		margin-bottom: .1rem;
	}
	.explanation{
		font-size: .28rem;
		text-align: left;
		padding: .4rem;
		line-height: 2;
		.title{
			margin-bottom: .1rem;
		}
	}
	
}
</style>
